const DeleteForm = props => {
  return (
    <form style={props.showDeleteForm?{display:"inline-block"}:{display:"none"}} action="" onSubmit={(e)=>{props.deleteMenuHandler(e, props.menu.id)}}>
      <div>
        Are you sure you want to delete the menu?
      </div>
      <input type="submit" value="DELETE" />
    </form>
  );
};

export default DeleteForm;