const EditButton = props=>{
  return (
    <button
      className="menu-add-btn"
      onClick={()=>{props.editMenuBtnHandler(props.menu.id)}} 
      style={{ marginLeft:"0px", padding:"3px", fontSize:'9px', border:"none" , backgroundColor:'white'}}
    >
      <i 
        style={{marginRight:'2px'}}
        className="fa fa-edit "
      ></i>
      Edit
    </button>
  );
}

export default EditButton;