import { useState } from "react";

const EditMenuOrPageForm = props=>{

  const [editMenuNameValue, setEditMenuNameValue] = useState(props.menu.name);
  const editMenuNameChangeHandler = (e)=>{
    setEditMenuNameValue( e.target.value);
  };

  const [editMenuOrderValue, setEditMenuOrderValue]  = useState(props.menu.order);
  const editMenuOrderChangeHandler = (e)=>{
    setEditMenuOrderValue(e.target.value);
  };

  const [editMenuIconValue, setEditMenuIconValue] = useState(props.menu.icon);
  const editMenuIconChangeHandler = (e)=>{
    setEditMenuIconValue(e.target.value);
  }

  return (
    <form style={props.showEditForm?{display:"inline-block"}:{display:"none"}} action="" onSubmit={(e)=>{props.editMenuOrPageHandler(e, props.menu.id)}}>
      <input type="text" name="name" placeholder="File Name" value={editMenuNameValue} onChange={editMenuNameChangeHandler} />
      <select name="parentId" id="">
        <option value="currentParent">Current Parent</option>
        {props.menuFromApi.map(item=>{
          return <option value={item.id} key={item.id}>{item.id} - {item.name}</option>
        })}
      </select>
      <input type="text" name="icon" placeholder="Icon (ex: fas fa-sticky-note)" value={editMenuIconValue} onChange={editMenuIconChangeHandler} />
      <input type="text" name="order" placeholder="Order" value={editMenuOrderValue} onChange={editMenuOrderChangeHandler} />
      <input type="submit" value="Save" />
    </form>
  );
}

export default EditMenuOrPageForm;