const AddPageForm = props=>{
  return (
    <form style={props.showAddPageForm?{display:"inline-block"}:{display:"none"}} action="" onSubmit={(e)=>{props.addPageHandler(e, props.menu.id)}}>
      <input type="text" name="name" placeholder="Page Name" />
      <input type="text" name="icon" placeholder="Icon (ex: fas fa-sticky-note)" />
      <input type="submit" value="Add A Page" />
    </form>
  );
};

export default AddPageForm;