import { useState } from "react";
import TextEditor from "../Editor/TextEditor";

const PageTextEditor = props=>{
  const [showEditor, setShowEditor] = useState(false);

  return (
    <div onClick={()=>{showEditor ? setShowEditor(false) : setShowEditor(true)}}>
      {showEditor || (props.content.length === 0) ? 
      <TextEditor
        onChange={props.onChange}
        index={props.index}
        id={props.id}
        content={props.content}
      /> 
      : 
      <div dangerouslySetInnerHTML={{__html: props.content}} style={{padding:"10px"}}/>
      }
    </div>
  );
};

export default PageTextEditor;