const AddMenuForm = props=>{
  return (
    <form style={props.showAddMenuForm?{display:"inline-block"}:{display:"none"}} action="" onSubmit={(e)=>{props.addSubMenuHandler(e, props.menu.id)}} >
      <input type="text" name="name" placeholder="Sub Menu Title" />
      <input type="text" name="icon" placeholder="Icon (ex: fas fa-bar)"/>  
      <button className="btn btn-secondary">Add A Sub Menu</button>
    </form>
  );
};

export default AddMenuForm;