import { Link } from "react-router-dom";
import Menu4 from "./Menu4";
import IconsInLink from "../SidebarComponents/IconsInLink";

const Menu3 = (props) => {
  let menu3 = props.menu3;
  let menuFromApi = props.menuFromApi;

  // let menu2used = false;

  const parentMenu = (e, id) => {
    let hasChild = false;

    for (let tempItem of props.menuFromApi) {
      if (tempItem.parentId === id) {
        hasChild = true;
        break;
      }
    }

    if (hasChild) {
      e.preventDefault();
    }
    props.activeElementIdHandler(id);
  };

  const menu2res = () => {
    // console.log("menu3.parentId", menu3.parentId);

    // if (menu3.parentId == 3) {
    if (menu3.parentId === props.currentId) {
      // if (true) {

      return (
        <ul className="show-dropdown">
          <li
            className={
              props.activeElementIds.includes(menu3.id) ? "active" : ""
            }
          >
            {/* <Link
              to={menu3.link ? menu3.link : '#'}
              onClick={(e) => {
                parentMenu(e, menu3.id);
              }}
            >
              {menu3.icon ? <i className={menu3.icon}></i> : ""}
              {menu3.name}
            </Link> */}

            <IconsInLink
              menu={menu3}
              newMenuHandler={props.newMenuHandler}
              editMenuHandler={props.editMenuHandler}
              deleteMenuHandler={props.deleteMenuHandler}
              activeElementIdHandler={props.activeElementIdHandler}
              menuFromApi={props.menuFromApi}
            />

            {menuFromApi.map((menu4) => {
              return (
                <Menu4
                  key={menu4.id}
                  menu4={menu4}
                  currentId={menu3.id}
                  menuFromApi={menuFromApi}
                  activeElementIdHandler={props.activeElementIdHandler}
                  activeElementIds={props.activeElementIds}
                />
              );
            })}
          </li>
        </ul>
      );
    }
  };

  return <>{menu2res()}</>;
};

export default Menu3;
